.app__footer {
    width: 100%;
    position: relative;
    z-index: 1;
  
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    background: var(--color-black);
    padding-top: 0;
  }
  
  .app__footer-links {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      margin-top: 5rem;
      padding: 0 2rem;
  }
  
  .app__footer-links_contact,
  .app__footer-links_logo,
  .app__footer-links_work {
      flex: 1;
      margin: 1rem;
      text-align: center;
  }
  
  .app__footer-links_logo img:first-child {
      width: 240px;
      margin-bottom: 1rem;
      padding-bottom: 1rem;
  }

  .app__footer-links_logo p {
      color: var(--color-golden);
  }
  
  .app__footer-links_work p:nth-child(2n + 1) {
      margin-bottom: 1rem;
  }
  
  .app__footer-links_icons {
      margin-top: 0.5rem;
  }
  
  .app__footer-links_icons svg {
      color: #fff;
      margin: 0.5rem;
      font-size: 24px;
      cursor: pointer;
  }
  
  .app__footer-links_icons svg:hover {
      color: var(--color-golden);
  }
  
  .app__footer-headtexts {
      font-family: var(--font-base);
      color: var(--color-golden);
      font-size: 35px;
      line-height: 50px;
      margin-bottom: 1rem;
  }
  
  .footer__copyright {
      margin-top: 3rem;
  }

  .app__footer-links_contact a :hover {
    text-decoration: underline;
    color: #fff;
    transition: 0.3s ease;
    opacity: 0.5;
  }
  
  @media screen and (max-width: 2000px) {
      .app__footer-headtext {
          font-size: 51px;
          line-height: 61px;
      }
  }
  
  @media screen and (max-width: 1150px) {
      .app__footer-links {
          align-items: center;
          flex-direction: column;
          padding: 0;
      }
      
  }
  
  @media screen and (max-width: 650px) {
      .app__footer {
          padding: 0 0 2rem 0;
      }
      #contact {
        margin-top: -3rem;
      }
  }
  
 